* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: purple;
}

.box {
  background-color: purple;
  text-align: center;
  justify-content: center;
  padding-top:18%;
  height: 100vh;
}
.h11{
  margin-bottom: 1%;
  margin-top:-12%;
  color: white;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-shadow: 3px 2px #000000;
  font-size: 3.5vw;
}
.h13{
  margin-bottom: 2%;
}
.inp1{
  margin-bottom: 2%;
  height: 7vh;
  width:60vw;
  background-color: rgb(210, 206, 206);
  border-radius: 10px;
  border:1px solid rgb(210, 206, 206);
  text-align: center;
  font-size: 1.7vw;
}
.btn{
  height:7vh;
  width:13vw;
  font-weight: 600;
  color: aliceblue;
  background-color: blueviolet;
  border:2px solid rgb(255, 255, 255);
  border-radius: 30px;
  font-size: 1.4vw;
  margin-bottom: 5%;
}
